.about1 {
    height: 100vh;
    overflow: hidden;
    padding-top: 8rem;
}

.about1__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about1__me {
    position: relative;
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
    animation: change-bg 10s ease-in-out infinite;
    animation-delay: 3s;
}

@keyframes change-bg {
    0% {
        background: linear-gradient(45deg, 
        transparent, var(--color-primary), 
        transparent);
    }

    100% {
        background: linear-gradient(45deg, 
        transparent, var(--color-primary-variant), 
        transparent);
    }
}

.about1__me-image {
    position: absolute;
    border-radius: 2rem;
    overflow: hidden;
    transform: scale(1);
    transition: var(--transition);
    bottom: 0;
    left: 0;
    right: 0;
}

.about1__me-image:hover {
    transform: scale(.5);
}

.about1__info {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.info-about1,
.info-about2,
.info-about3 {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background: var(--color-bg-variant);
    border-radius: 69% 31% 66% 34%/ 21% 30% 70% 79%;
    border: 2px solid var(--color-primary-variant);
    padding: 2.5rem;
    z-index: 100;
    transition: var(--transition);
    animation: morph 6s linear infinite;
}

@keyframes morph {
    0%,
    100% {
        border-radius: 40% 60% 70% 30% / 40% 40% 60% 50%;
    }

    30% {
        border-radius: 70% 30% 50% 50% / 30% 30% 70% 70%;
    }

    60% {
        border-radius: 100% 60% 60% 100% / 100% 100% 60% 60%;
    }

    80% {
        border-radius: 69% 31% 66% 34% / 21% 30% 70% 79%;
    }
}

.info-about1:hover,
.info-about2:hover,
.info-about3:hover {
    background: transparent;
    cursor: pointer;
}

.about1__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about1__info h5 {
    font-size: .95rem;
}

.about1__info small {
    font-size: .7rem;
    color: var(--color-light);
}

.about1__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

@media screen and (max-width: 1024px) {
    .about1 {
        overflow: scroll;
        height: fit-content;
    }

    .about1__container {
        grid-template-columns: 100%;
        gap: 4rem;
        padding-top: 5rem;
    }

    .about1__me {
        order: 2;
    }

    .about1__content {
        order: 1;
    }

    .about1__me {
        width: 70%;
        margin: 0 auto;
    }

    .about1__content p {
        margin: 2.6rem 0 2.6rem;
    }
}

@media screen and (max-width: 600px) {
    .about1__info {
        grid-template-columns: 1fr 1fr ;
    }

    .info-about3 {
        grid-column: 1/ span 2;
        text-align: center;
    }

    .about1__me {
        width: 85%;
    }
}





