header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: transparent;
    height: 4rem;
}

.logo {
    font-size: 2rem;
    font-weight: 700;
    color: var(--color-white);
    cursor: default;
    opacity: 0;
    animation: slideRight 1.5s ease forwards;
    animation-delay: .2s;
}

.logo:hover {
    color: var(--color-white);
}

.navbar {
    display: flex;
    gap: 4rem;
}

.navbar a {
    opacity: 0;
    animation: slideTop 1.5s ease forwards;
    animation-delay: calc(.2s * var(--i));
}

.navbar a:nth-child(1) {
    --i:2
}

.navbar a:nth-child(2) {
    --i:3
}

.navbar a:nth-child(3) {
    --i:4
}

.navbar a:nth-child(4) {
    --i:5
}

.navbar a:nth-child(5) {
    --i:6
}

.navbar a:nth-child(6) {
    --i:7
}

.navbar a:nth-child(7) {
    --i:8
}

.mobile__menu {
    display: none;
}

/********************change navbar style on scroll*****************/
.window-scroll {
    background: var(--color-primary-variant);
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.5);
}

/********************key frames ****************/
@keyframes slideRight {
    0% {
        transform: translateX(-7rem);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slideTop {
    0% {
        transform: translateY(7rem);
        opacity: 0;
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

/******************media queries (tablets) ****************/
@media screen and (max-width: 1024px) {
    .logo {
        margin-left: -5rem;
    }
    .mobile__menu {
        display: flex;  
    }

    .mobile__menu button {
        font-size: 1.8rem;
        border-radius: 20%;
        padding-top: 0.4rem;
        cursor: pointer;
        background: transparent;
        color: var(--color-white);   
        margin-right: -5rem;
    }

    .open__menu {
        opacity: 0;
        animation: slideLeft 1.5s ease forwards;
        animation-delay: .5s;
    }

    @keyframes slideLeft {
        0% {
            transform: translateX(7rem);
            opacity: 0;
        }
    
        100% {
            transform: translateX(0);
            opacity: 1;
        }
    }

    .navbar {
        position: fixed;
        top: 5rem;
        right: 5%;
        height: fit-content;
        flex-direction: column;
        width: 18rem; 
        gap: 2px;
    }

    .navbar__hidden {
        display: none;
    }

    .navbar a {
        background: var(--color-primary-variant);
        box-shadow: -4rem 6rem 10rem rgba (0, 0, 0, 0.6);
        height: 5.8rem;
        width: 100%;
        display: grid;
        place-items: center;
        animation: animateNavItems 1s linear forwards;
        animation-delay: calc(.1s * var(--i));
        transform-origin: top right;
        opacity: 0;
    }


    @keyframes animateNavItems {
        from {
            transform: rotateZ(-90deg) rotateX(90deg) scale(0.1)
        } 

        to {
            transform: rotateZ(0) rotateX(0) scale(1);
            opacity: 1;
        }
    }
}