.services {
    height: 100vh;
    overflow: hidden;
    padding-top: 5rem;
}

.section-services {
    margin-top: 10rem;
   /* display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;*/
    gap: 2rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.section-services .service-box {
   /* flex: 1 1 18rem;*/
  
   
    padding: 2rem 1rem 2rem;
    text-align: center;
    background: var(--color-bg-variant);
    transition: transform 0.4s;
    border-radius: 10px;
    position: relative;
}

.service-box:hover {
    transform: translateY(-.7rem);
}

.service-icon {
    font-size: 3.5rem;
    color: var(--color-primary);
    position: absolute;
    top: -5rem;
    left: 50%;
    transform: translateX(-50%);
}

.service-box h3 {
    margin-top: 10px;
    font-size: 1.5rem;
}

.service-box p {
    color: var(--color-light);
    margin: .5rem 0 1.5rem 0;
    letter-spacing: 1px;
    line-height: 1.6;
}

@media screen and (max-width: 1024px) {
    .services {
        height: auto;
        overflow: scroll;
        padding-top: 4rem;
    }

    .section-services {
        grid-template-columns: 1fr 1fr;
        row-gap: 8rem;
    }
}

@media screen and (max-width: 1024px) {
    .section-services {
        grid-template-columns: 1fr;
    }
}


 