.home {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
    display: flex;
    justify-content: space-around;
    align-items: center;
} 

.home__content h3:nth-of-type(1) {
    text-align: center;
    color: var(--color-light);
    animation: slideBottom 1.5s ease forwards;
    animation-delay: 1.5s;
    opacity: 0;
}

@keyframes slideBottom {
    0% {
        transform: translateY(-200px);
        opacity: 0;
    }

    100% {
        transform: translateY(0);
        opacity: 1; 
    }
}

.home__content h1 {
    margin: -3px 0;
    text-align: center;
    opacity: 0;
    animation: slideRight 1.5s ease forwards;
    animation-delay: 1.5s;
}

@keyframes slideRight {
    0% {
        transform: translateX(-200px);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

.home__content h3:nth-of-type(2) {
    text-align: center;
    animation: slideTop 1.5s ease forwards;
    animation-delay: 1.5s;  
    opacity: 0;
}

.multiple-text {
    color: var(--color-special);
    font-size: 1.5rem;
}

@keyframes slideTop {
    0% {
        transform: translateY(200px);
        opacity: 0;
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

.home__content p {
    color: var(--color-light);
    margin: 1rem auto;
    padding: 1rem 1rem;
    opacity: 0;
    animation: slideLeft 1.5s ease forwards;
    animation-delay: 2s;
}

@keyframes slideLeft {
    0% {
        transform: translateX(200px);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

.social__media a svg{
    margin-left: .25rem;
    font-size: 1.7rem;
    opacity: 0;
    animation: slideLeft 1.5s ease forwards;
    animation-delay: calc(.2s * var(--i));
}

.social__media a:nth-child(1) svg {
    --i:12
}

.social__media a:nth-child(2) svg {
    --i:13
}

.social__media a:nth-child(3) svg {
    font-size: 1.78rem;
    --i:14
}

.social__media a:nth-child(4) svg {
    --i:15
}

.cta {
    margin-top: 3rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.cta a {
    opacity: 0;
    animation: slideTop 1.5s ease forwards;
    animation-delay: calc(.2s * var(--i));
}

.cta a:first-child {
    --i:16
}

.cta a:last-child{
    --i:18
}


.home__img img {
    max-width: 30rem;
    border-bottom-right-radius: 50%;
    border-bottom-left-radius:  50%; 
    opacity: 0;
    animation: zoomIn 1s ease forwards, floatImage 4s ease-in-out infinite;
    animation-delay: 5s, 8s;     
}

@keyframes zoomIn {
    0% {
        transform: scale(0);
        opacity: 0;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes floatImage {
    0% {
        transform: translateY(0);
    }
    
    50% {
        transform: translateY(-1rem);
    }

    100% {
        transform: translateY(0);
    }
}

/*****************media queries(tablets)****************/
@media screen and (max-width: 1024px) {
    .home {
        padding-top: 10rem;
        flex-direction: column;
        overflow: scroll;
        height: fit-content;
    }

    .home__content {
        padding-bottom: 1rem;
    }

    .home__img img {
        width: 30rem; 
    }
}

/*****************media queries(phones)****************/
@media screen and (max-width: 600px) {
    .home__content h1 {
        font-size: 2.5rem;
    }

    .home__content p {
        font-size: .9rem;
    }

    .home__img img {
        width: 22rem; 
    }

    .multiple-text {
    font-size: 1.3rem;
    }
}      



