.skills {
    height: auto;
    overflow: hidden;
    padding-top: 5rem;
}

.skill-main {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 2.8rem;
    grid-column-gap: 3.2rem;
}

.skill-main h3 {
    text-align: center;
    font-size: 2rem;
    color: var(--color-light);
    margin-bottom: 2rem;
}

.skill-bar {
    margin-bottom: 2.3rem;
}

.skill-left .skill-bar .info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 .6rem;
}

.info p:first-child {
    color: var(--color-light);
}

.skill-left .skill-bar .bar {
    width: 100%;
    height: 10px;
    background-color: var(--color-bg-variant);
    border-radius: 25px;
    margin-top: 10px;
    position: relative;
}

.skill-bar .bar span {
    width: 50%;
    height: 100%;
    position: absolute;
    left: 0;
    background: var(--color-primary);
    border-radius: 25px;
}

.skill-bar .bar .html {
    width: 72%;
    animation: html 2s;
}

.skill-bar .bar .figma {
    width: 90%;
    animation: figma 3s;
}

.skill-bar .bar .javascript {
    width: 80%;
    animation: javascript 4s;
}

.skill-bar .bar .css {
    width: 62%;
    animation: css 5s;
}

@keyframes html {
    0% {
        width: 0%;
    }

    100% {
        width: 72%;
    }
}

@keyframes figma {
    0% {
        width: 0%;
    }

    100% {
        width: 90%;
    }
}

@keyframes javascript {
    0% {
        width: 0%;
    }

    100% {
        width: 80%;
    }
}

@keyframes css {
    0% {
        width: 0%;
    }

    100% {
        width: 62%;
    }
}

.professional {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.box {
    position: relative;
    margin: .6rem 0;
    flex: 1 1 15rem;
}


.circle {
    width: 100%;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.circle .points {
    width: 5px;
    height: 10px;
    background-color: var(--color-bg-variant);
    position: absolute;
    border-radius: 3px;
    transform: rotate(calc(var(--i) * var(--rot))) translateY(-45px);
}

.points.marked {
    animation: glow 0.04s linear forwards;
    animation-delay: calc(var(--i) * 0.09s);
}

@keyframes glow {
    0% {
        background: var(--color-bg-variant);
    }

    100% {
        background: var(--color-primary);
    }
}

.box .text {
    text-align: center;
    color: #fff;
    font-size: 1rem;
}

.box .text big {
    font: 400;
    letter-spacing: 1px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.box .text small {
    display: block;
    font-weight: 600;
    color: var(--color-light);
}

@media screen and (max-width: 1024px) {
    .skills {
        height: auto;
        overflow: scroll;
    }

    .skill-main {
        grid-template-columns: 1fr;
    }
}
