.portfolio {
    height: auto;
    overflow: scroll;
    padding-top: 5rem;
}

.filter-buttons {
    margin: 2rem;
    display: flex;
    justify-content: center;
}

.button {
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: .4px;
    margin-left: 1.3rem;
    transition: var(--transition);
}

.button:hover {
    color: var(--color-light);
}

.portfolio-gallery {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
    gap: 2rem;
}

.port-box {
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
    border: 2px solid var(--color-primary);
    display: flex;
    grid-template-rows: 1fr auto;
    align-items: center;
}

.port-image {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
}

.port-image img {
    width: 100%;
    opacity: .5s;
    height: 100%;
    transition: .5s;
    object-fit: cover;
}

.port-content {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background: linear-gradient(rgba(0,0,0,.2), var(--color-primary-variant));
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    transition: .5s;
    padding: 0 2rem;
}

.port-box:hover .port-content {
    opacity: 1;
}

.port-box:hover .port-image img {
    transform: scale(1.1);
}


.port-content h3 {
    font-size: 1.5rem;
    font-weight: 600;
}

.port-content p {
    font-size: .8rem;
    margin: 5px 0 15px 0;
}

.port-content a {
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    outline: 2px solid #fff;
}

.icon {
    font-size: 1.3rem;
}

@media screen and (max-width: 1400px) {
    .filter-buttons {
        margin: 2.5rem 0;
    }

    .port-content h3 {
        font-size: 1.2rem;
    }

    .port-content p {
        font-size: .7rem;
    }

}

@media screen and (max-width: 1024px) {
    .portfolio {
        padding-top: 4rem;
    }


    .port-content h3 {
        font-size: 1.5rem;
    }

    .port-content p {
        font-size: .8rem;
    }
}