@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: #064470;
    --color-white : #fff;
    --color-light: rgba(255, 255, 255, 0.6);
    --color-special: #08f7af;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

    --transition: all 500ms ease;
    --transition-variant: all 3s ease;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background: url(https://wallpaperaccess.com/full/4441654.jpg) fixed;
}

/****************general styles****************/
.container {
    width: var(--container-width-lg);
    margin: 0 auto ;
}

h1, h2, h3, h4, h5 {
    font-weight: 700;
}

h1 {
    font-size: 3.5rem;
}

section {
    margin-top: 8rem;
}

section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

a {
    color: var(--color-white);
    transition: var(--transition);
}

a:hover {
    color: var(--color-light);
}

.btn, 
.btn-primary {
    position: relative;
    width: max-content;
    display: inline-block;
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    border: 1px solid var(--color-primary);
    cursor: pointer;
    transition: var(--transition);
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
    position: relative;
}

.btn:hover {
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary:hover {
    color: var(--color-white);
    border-color: var(--color-primary);
    background: transparent;
}

.btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    border-radius: 0.4rem;
    background: var(--color-primary);
    z-index: -1;
    transition: var(--transition);
}

.btn-primary::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    border-radius: 0.4rem;
    background: rgba(0, 0, 0, 1);
    z-index: -1;
    transition: var(--transition);
}

.btn:hover::before,
.btn-primary:hover::before {
    width: 100%;
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/****************general meadia queries (tablets)****************/
@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/********************general meadia queries(phones)****************/
@media screen and (max-width: 60px) {
    .container {
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}

/*********parallax****************/
.scroll-scale {
    opacity: 0;
    transform: scale(0);
    transition: var(--transition-variant);
}

.scroll-top {
    opacity: 0;
    transform: translateY(200px);
    transition: var(--transition-variant);
}

.scroll-bottom {
    opacity: 0;
    transform: translateY(-200px);
    transition: var(--transition-variant);
}

.scroll-left {
    opacity: 0;
    transform: translateX(200px);
    transition: var(--transition-variant);
}

.scroll-right {
    opacity: 0;
    transform: translateX(-200px);
    transition: var(--transition-variant);
}

.show-items {
    opacity: 1;
    transform: translateX(0);
    transform: translateY(0);
    transform: scale(1);
}
